.App-logo {
  height: 40vmin;
  pointer-events: none;
  z-index: 10;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: fadeIn 10s linear;
  }
}

.logo-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.video-background {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}

.content-bottom {
  position: fixed;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  color: #f1f1f1;
  padding: 20px;
}

.btn-transp {
  width: 200px;
  font-size: 18px;
  padding: 10px;
  background: #0000;
  color: #fff;
  cursor: pointer;
  border: thin solid yellow;
}

.btn-transp:hover {
  color: yellow;
}

.btn-transp-round {
  font-size: 18px;
  padding: 5px;
  margin-left: 10px;
  margin-right: 10px;
  background: #0000;
  color: #fff;
  cursor: pointer;
  border: thin solid yellow;
  border-radius: 20px;
}

.btn-transp-round:hover {
  color: yellow;
}

.input-transp {
  width: 200px;
  font-size: 18px;
  padding: 10px;
  background: #0000;
  color: #fff;
  // cursor: pointer;
  border: thin solid yellow;
}

/*** Home ***/
.panels {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  color: #f1f1f1;
}

.panel-ad {
  width: 20%;
  height: 100%;
  background-color: #00ffff33;
  display: inline-block;
  z-index: 100;
  animation: scale-up-ver-top 5s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
.panel-main {
  width: 60%;
  height: 100%;
  display: inline-block;
  z-index: 100;
}
.panel-chat {
  width: 20%;
  height: 100%;
  background-color: #33dd0033;
  display: inline-block;
  z-index: 100;
  animation: scale-up-ver-bottom 5s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

@keyframes scale-up-ver-bottom {
  0% {
    transform: scaleY(0.1);
    transform-origin: 0% 100%;
  }
  100% {
    transform: scaleY(1);
    transform-origin: 0% 100%;
  }
}

@keyframes scale-up-ver-center {
  0% {
    transform: scaleY(0.4);
  }
  100% {
    transform: scaleY(1);
  }
}

@keyframes scale-up-ver-top {
  0% {
    transform: scaleY(0.4);
    transform-origin: 100% 0%;
  }
  100% {
    transform: scaleY(1);
    transform-origin: 100% 0%;
  }
}
